@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&family=Roboto:wght@300;400&display=swap);
:root {
	--primary-400: #1f1f25;
	--primary-500: #292a31;
	--primary-600: #3c3e44;
}

:root:has(.app.dark-blue-tint) {
	--primary-400: #0b0c27;
	--primary-500: #191a35;
	--primary-600: #2b2c44;
}

.app.dark-blue-tint {
	--primary-400: #0b0c27;
	--primary-500: #191a35;
	--primary-600: #2b2c44;
}

:root:has(.app.dark) {
	--primary-400: #1f1f25;
	--primary-500: #292a31;
	--primary-600: #3c3e44;
}

.app.dark {
	--primary-400: #1f1f25;
	--primary-500: #292a31;
	--primary-600: #3c3e44;
}

:root:has(.app.dark-contrast) {
	--primary-400: #010001;
	--primary-500: #18191d;
	--primary-600: #2d2f35;
}

.app.dark-contrast {
	--primary-400: #010001;
	--primary-500: #18191d;
	--primary-600: #2d2f35;
}

.app {
	background-color: #1f1f25;
	background-color: var(--primary-400);
	isolation: isolate;
}

*,
*::after,
*::before {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	transition: color 0.5s ease-in, background 0.5s ease-in, background-color 0.5s ease-in;
	/* color: black !important; */
	-webkit-tap-highlight-color: transparent;
}

body {
	font-family: poppins, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

*::-webkit-scrollbar {
	width: 10px;
}
*::-webkit-scrollbar-track {
	background-color: #3c3e44;
}

*::-webkit-scrollbar-thumb {
	border: 2px solid #3c3e44;
	border-radius: 100vmax;
	background-color: #1f1f25;
}

.topZ {
	z-index: 99 !important;
}
:root {
	font-size: calc(1rem - 2px);
	color: white;
}

img {
	max-width: 100%;
	display: block;
}

button,
input,
textarea {
	border: none;
	outline: none;
	font-family: inherit;
	background: none;
}

button,
a {
	cursor: pointer;
	text-decoration: none;
	color: inherit;
}

.roundimage {
	aspect-ratio: 1;
	border-radius: 50%;
	object-fit: cover;
}

.container {
	max-width: 1500px;
	min-height: 100vh;
	padding-inline: 2rem;
	margin-inline: auto;
	display: flex;
	flex-direction: column;
	padding-bottom: 2rem;
}

button:disabled {
	cursor: not-allowed;
}

input::-webkit-input-placeholder {
	font-size: 0.9rem;
}

input::placeholder {
	font-size: 0.9rem;
}

.green {
	position: relative;
}

.sidebar {
	position: fixed;
	display: none;
	max-width: 400px;
	opacity: 0;
	z-index: 999;
	border-radius: 10px;
	top: 100px;
	bottom: 10px;
	left: 0;
	right: 1rem;
	-webkit-transform: translateX(-20%);
	        transform: translateX(-20%);
	pointer-events: none;
}

.green::after {
	content: "";
	position: absolute;
	width: 30%;
	aspect-ratio: 1;
	background-color: rgb(21, 214, 31);
	border-radius: 50%;
	right: 0;
	bottom: 0;
}

.gradient-border {
	position: relative;
}

.gradient-border::before {
	content: "";
	position: absolute;
	inset: -2px;
	border-radius: inherit;
	z-index: -1;
	background: linear-gradient(145deg, #3c3e44, transparent);
	background: linear-gradient(145deg, var(--primary-600), transparent);
}

@media (max-width: 1000px) {
	.container {
		padding-inline: 0.5rem;
	}
}

@media (max-width: 800px) {
	.container {
		max-width: 500px;
	}
	.sidebar {
		display: block;
		visibility: hidden;
		transition: all 0.2s linear;
	}
	.visible {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
		visibility: visible;
		opacity: 1;
		pointer-events: all;
		box-shadow: 0 10px 100px rgb(0, 0, 0);
		transition: opacity 0.2s linear, -webkit-transform 0.2s linear;
		transition: opacity 0.2s linear, transform 0.2s linear;
		transition: opacity 0.2s linear, transform 0.2s linear, -webkit-transform 0.2s linear;
	}
}

.email {
	position: relative;
}

.email-datalist {
	position: absolute;
	background-color: #1f1f25;
	background-color: var(--primary-400);
	display: grid;
	border-radius: 10px;
	inset: 80% 0 auto 0;
	max-height: 12rem;
	overflow: auto;
}

.email-datalist option {
	padding-inline: 1rem;
	display: block;
	height: 3rem;
	display: grid;
	align-items: center;
	cursor: pointer;
	transition: background-color 0.2s linear;
	font-size: 0.8rem;
}

.email-datalist option:hover {
	background-color: #3c3e44;
	background-color: var(--primary-600);
}

.loadingBox {
	position: relative;
	background-color: #3c3e44;
	background-color: var(--primary-600);
	overflow: hidden;
}

.loadingBox::after {
	position: absolute;
	content: "";
	height: 500%;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	width: 30px;
	background: #1f1f25;
	background: var(--primary-400);
	-webkit-filter: blur(50px);
	        filter: blur(50px);
	left: -50%;
	-webkit-animation: loading 1s linear infinite;
	        animation: loading 1s linear infinite;
}

@-webkit-keyframes loading {
	to {
		left: 150%;
	}
}

@keyframes loading {
	to {
		left: 150%;
	}
}

.auth {
	-webkit-perspective: 1200px;
	        perspective: 1200px;
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 1rem;
}

.login,
.register {
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
	transition: -webkit-transform 0.4s linear;
	transition: transform 0.4s linear;
	transition: transform 0.4s linear, -webkit-transform 0.4s linear;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

.register {
	-webkit-transform: translate(-50%, -50%) rotateY(-180deg);
	        transform: translate(-50%, -50%) rotateY(-180deg);
}

.signup .login {
	-webkit-transform: translate(-50%, -50%) rotateY(180deg);
	        transform: translate(-50%, -50%) rotateY(180deg);
}

.signup .register {
	-webkit-transform: translate(-50%, -50%) rotateY(0deg);
	        transform: translate(-50%, -50%) rotateY(0deg);
}

.auth p {
	margin-inline: auto;
	line-height: 1.8em;
	width: -webkit-max-content;
	width: max-content;
	margin-top: 1.5rem;
	text-align: center;
}

.auth form {
	background-color: var(--primary-500);
	color: white;
	width: 400px;
	padding: 3rem 1.5rem 1.5rem 1.5rem;
	border-radius: 10px;
}

.auth label {
	display: block;
	margin-bottom: 0.5rem;
	padding-left: 0.3rem;
	font-weight: 300;
	font-size: 0.85rem;
}

.auth input {
	background-color: var(--primary-600);
	width: 100%;
	padding: 0.75rem 1rem;
	color: white;
	border-radius: 10px;
	margin-bottom: 2rem;
}

.auth button {
	background: var(--primary-400);
	width: 100%;
	color: white;
	padding: 0.75rem;
	border-radius: 10px;
	font-size: 1rem;
}

.auth p span:hover {
	cursor: pointer;
	text-decoration: underline;
}

@media (max-width: 420px) {
	.auth form {
		width: 350px;
	}
}

#modals {
	position: fixed;
	bottom: 2rem;
	right: 2rem;
	z-index: 999;
	display: flex;
	flex-direction: column;
	grid-gap: 0.5rem;
	gap: 0.5rem;
	pointer-events: none;
	--direction: 1;
}

.modal {
	background-color: var(--primary-600);
	color: white;
	min-width: 20rem;
	max-width: 30rem;
	-webkit-transform: translateX(calc(var(--direction) * 150%));
	        transform: translateX(calc(var(--direction) * 150%));
	max-height: 0;
	padding: 0;
	overflow: hidden;
	pointer-events: none;
	border-radius: 5px;
	text-align: center;
	border-left: 3px solid white;
	-webkit-animation: in 5s ease-out forwards;
	        animation: in 5s ease-out forwards;
}

@-webkit-keyframes in {
	5% {
		padding: 1rem;
		max-height: 500px;
		-webkit-transform: translateX(calc(var(--direction) * 150%));
		        transform: translateX(calc(var(--direction) * 150%));
	}
	8% {
		padding: 1rem;
		-webkit-transform: translateX(calc(var(--direction) * 10px));
		        transform: translateX(calc(var(--direction) * 10px));
		max-height: 500px;
	}
	11% {
		padding: 1rem;
		-webkit-transform: translateX(0);
		        transform: translateX(0);
		max-height: 500px;
	}
	95% {
		padding: 1rem;
		-webkit-transform: translateX(0);
		        transform: translateX(0);
		max-height: 500px;
	}
	96% {
		padding: 1rem;
		-webkit-transform: translateX(calc(var(--direction) * -10px));
		        transform: translateX(calc(var(--direction) * -10px));
		max-height: 500px;
	}
	100% {
		padding: 1rem;
		-webkit-transform: translateX(calc(var(--direction) * 150%));
		        transform: translateX(calc(var(--direction) * 150%));
		max-height: 500px;
	}
}

@keyframes in {
	5% {
		padding: 1rem;
		max-height: 500px;
		-webkit-transform: translateX(calc(var(--direction) * 150%));
		        transform: translateX(calc(var(--direction) * 150%));
	}
	8% {
		padding: 1rem;
		-webkit-transform: translateX(calc(var(--direction) * 10px));
		        transform: translateX(calc(var(--direction) * 10px));
		max-height: 500px;
	}
	11% {
		padding: 1rem;
		-webkit-transform: translateX(0);
		        transform: translateX(0);
		max-height: 500px;
	}
	95% {
		padding: 1rem;
		-webkit-transform: translateX(0);
		        transform: translateX(0);
		max-height: 500px;
	}
	96% {
		padding: 1rem;
		-webkit-transform: translateX(calc(var(--direction) * -10px));
		        transform: translateX(calc(var(--direction) * -10px));
		max-height: 500px;
	}
	100% {
		padding: 1rem;
		-webkit-transform: translateX(calc(var(--direction) * 150%));
		        transform: translateX(calc(var(--direction) * 150%));
		max-height: 500px;
	}
}

.modal p {
	font-size: 0.9rem;
}

@media (max-width: 800px) {
	#modals {
		right: initial;
		left: 1rem;
		--direction: -1;
	}
}

.loading {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	color: white;
	text-align: center;
	padding: 2rem;
	background-color: var(--primary-400);
	border-radius: 10px;
	max-width: 90vw;
}

.loading__circle {
	width: 50px;
	aspect-ratio: 1;
	border-radius: 50%;
	border: 5px solid white;
	border-bottom-color: transparent;
	-webkit-transform-origin: center;
	        transform-origin: center;
	margin: 1rem auto;
	-webkit-animation: rotate 1s linear infinite;
	        animation: rotate 1s linear infinite;
}

.loading h2 {
	margin-bottom: 2rem;
}

.loading p {
	font-size: 0.9rem;
	max-width: 600px;
	line-height: 1.8em;
}

@-webkit-keyframes rotate {
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes rotate {
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.backdrop {
	position: fixed;
	inset: 0;
	display: grid;
	visibility: hidden;
	background-color: rgba(0, 0, 0, 0.544);
	place-content: center;
	z-index: 99;
	opacity: 0;
	-webkit-backdrop-filter: blur(0);
	        backdrop-filter: blur(0);
	pointer-events: none;
	transition: opacity 0.2s linear, visibility 0.2s linear, -webkit-backdrop-filter 0.2s linear;
	transition: opacity 0.2s linear, backdrop-filter 0.2s linear, visibility 0.2s linear;
	transition: opacity 0.2s linear, backdrop-filter 0.2s linear, visibility 0.2s linear, -webkit-backdrop-filter 0.2s linear;
}

.backdrop.show {
	opacity: 1;
	visibility: visible;
	pointer-events: all;
	-webkit-backdrop-filter: blur(0.3rem);
	        backdrop-filter: blur(0.3rem);
}

.backdrop.show .backdrop__content {
	-webkit-transform: translateY(0);
	        transform: translateY(0);
	opacity: 1;
}

.backdrop__content {
	border-radius: 10px;
	-webkit-transform: translateY(-30px);
	        transform: translateY(-30px);
	opacity: 0;
	transition: opacity 0.2s ease-out, -webkit-transform 0.3s ease-out;
	transition: transform 0.3s ease-out, opacity 0.2s ease-out;
	transition: transform 0.3s ease-out, opacity 0.2s ease-out, -webkit-transform 0.3s ease-out;
	margin: 1rem;
}

.theme-switch {
	position: fixed;
	left: 3rem;
	bottom: 7rem;
	height: 3rem;
	aspect-ratio: 1;
	border-radius: 50%;
	z-index: 5;
	/* border: 1px solid var(--primary-400); */
	box-shadow: 0 0 1rem rgba(0, 0, 0, 0.274);
}

.theme-switch:not(.open) {
	-webkit-animation: beat 3s ease-out infinite;
	        animation: beat 3s ease-out infinite;
}

.switch {
	width: 100%;
	height: 100%;
	display: grid;
	place-content: center;
	font-size: 1.5rem;
	color: white;
	cursor: pointer;
	position: relative;
	z-index: 2;
	background-color: var(--primary-600);
	border-radius: 50%;
	transition: -webkit-transform 0.3s ease-out;
	transition: transform 0.3s ease-out;
	transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.themes {
	height: 100%;
	background-color: var(--primary-600);
	position: absolute;
	top: 0;
	left: 50%;
	border-radius: 0vmax 100vmax 100vmax 0;
	z-index: 1;
	padding: 0.6rem;
	grid-gap: 0.5rem;
	gap: 0.5rem;
	display: flex;
	overflow: hidden;
	pointer-events: none;
	max-width: 0;
	transition: max-width 0.3s ease-out;
}

.theme-switch.open .themes {
	max-width: 300px;
	pointer-events: all;
	overflow: visible;
}

.theme-switch.open .switch {
	-webkit-transform: rotate(90deg);
	        transform: rotate(90deg);
}

.themes > div {
	height: 100%;
	aspect-ratio: 1;
	border-radius: 50%;
	background-color: white;
	color: var(--primary-400);
	border: 1px solid rgba(255, 255, 255, 0.473);
	cursor: pointer;
	position: relative;
	isolation: isolate;
}

.themes > div:first-of-type {
	margin-left: 1rem;
}

.themes .tooltip {
	position: absolute;
	background-color: white;
	width: -webkit-max-content;
	width: max-content;
	padding: 0.2rem 1rem;
	border-radius: 0.2rem;
	pointer-events: none;
	opacity: 0;
	top: 0;
	left: 50%;
	-webkit-transform: translate(-50%, -170%);
	        transform: translate(-50%, -170%);
	transition: opacity 0.2s ease-in;
}
.themes .tooltip::before {
	position: absolute;
	content: "";
	bottom: 0;
	left: 50%;
	border: 10px solid transparent;
	border-top-color: white;
	-webkit-transform: translate(-50%, 90%);
	        transform: translate(-50%, 90%);
}

.themes > div:hover .tooltip {
	opacity: 1;
}

.themes .dark {
	background: linear-gradient(-45deg, #1f1f25 50%, #474951 50%);
}

.themes .dark-contrast {
	background: linear-gradient(-45deg, #010001 50%, #242529 50%);
}

.themes .dark-blue-tint {
	background: linear-gradient(-45deg, #0b0c27 50%, #2b2c44 50%);
}

@-webkit-keyframes beat {
	50% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}
}

@keyframes beat {
	50% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}
}

@media (max-width: 800px) {
	.container > .theme-switch {
		display: none;
	}
	.theme-switch {
		position: absolute;
		right: 1rem;
		left: initial;
		bottom: 2rem;
	}
	.themes {
		left: initial;
		right: 50%;
		border-radius: 100vmax 0 0 100vmax;
	}
	.theme-switch.open .switch {
		-webkit-transform: rotate(-90deg);
		        transform: rotate(-90deg);
	}
	.themes > div:first-of-type {
		margin-left: 0;
	}
	.themes > div:last-of-type {
		margin-right: 1rem;
	}
}

.search-results {
	position: absolute;
	width: 100%;
	left: 0;
	height: 85vh;
	overflow: auto;
	top: calc(100% + 1rem);
	background-color: var(--primary-500);
	padding: 2rem 1.5rem;
	border-radius: 10px;
	box-shadow: 0 0 30px rgba(0, 0, 0, 0.466);
	color: white;
	display: flex;
	flex-direction: column;
	grid-gap: 1.5rem;
	gap: 1.5rem;
	scrollbar-width: none;
}

.search-results::-webkit-scrollbar {
	display: none;
}

.search-results h3 {
	font-size: 1.2rem;
	width: -webkit-min-content;
	width: min-content;
	margin-bottom: 1em;
	border-bottom: 2px solid white;
}

.search-results p {
	width: 90%;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	font-size: 0.9rem;
	color: rgba(255, 255, 255, 0.534);
	margin-bottom: 1em;
}

.search-results p:hover {
	text-decoration: underline;
}

@media (max-width: 800px) {
	.search-results {
		position: fixed;
		height: auto;
		inset: 7rem 1rem 1rem;
		width: initial;
	}
}

