	align-items: center;
	grid-gap: 1rem;
	gap: 1rem;
	position: -webkit-sticky;
	position: sticky;
	height: 7rem;
	top: 0rem;
	z-index: 2;
	background-color: var(--primary-400);
}

.appbar form {
	display: flex;
	align-items: center;
	padding: 0.75rem 1.5rem;
	background-color: var(--primary-500);
	width: 50%;
	max-width: 500px;
	border-radius: 10px;
	grid-gap: 1rem;
	gap: 1rem;
	position: relative;
}

.appbar form button {
	width: 20px;
	cursor: pointer;
