@media (max-width: 800px) {
	.appbar {
		grid-gap: 0.5rem;
		gap: 0.5rem;
	}
	.appbar form {
		padding: 0.75rem;
		flex: 1 1;
	}
	.hamburger {
		display: flex;
		align-items: center;
	}
	.appbar__profile {
		grid-gap: 1rem;
		gap: 1rem;
	}
	.home-icon {
		display: none;
	}
}

.createpost {
	background-color: var(--primary-500);
	padding: 2rem 1rem;
	border-radius: 10px;
}

.createpost textarea {
	box-sizing: border-box;
