}

.btns {
	margin-top: 1rem;
	display: flex;
	grid-gap: 1rem;
	gap: 1rem;
	width: -webkit-max-content;
	width: max-content;
	margin-left: auto;
}

.uploaded-image {
	width: 100%;
	border-radius: 10px;
	overflow: hidden;
	position: relative;
}

.uploaded-image img {
	width: 100%;
}

.uploaded-image .close-icon {
	position: absolute;
	right: 1rem;
	top: 1rem;
	background-color: var(--primary-500);
	height: 30px;
	aspect-ratio: 1;
	border-radius: 50%;
	display: grid;
	place-content: center;
	cursor: pointer;
}

@media (max-width: 800px) {
	.createpost {
		padding: 1rem;
	}
}

.editPost {
	width: min(90vw, 700px);
	max-height: 70vh;
	overflow: scroll;
	border-radius: 10px;
	scrollbar-width: 0;
}

.editPost::-webkit-scrollbar {
	display: none;
}

.online {
	height: 100%;
	overflow: scroll;
	background-color: var(--primary-500);
	border-radius: 10px;
	color: white;
	scrollbar-width: none; /*to work in moz*/
	padding: 2rem;
	display: flex;
	flex-direction: column;
	grid-gap: 1.5rem;
	gap: 1.5rem;
}

.online::-webkit-scrollbar {
	display: none;
}

.online h2 {
	font-size: 1.1rem;
	font-weight: 500;
}

.online .user {
	position: relative;
	display: flex;
	color: white;
	align-items: center;
	grid-gap: 1rem;
	gap: 1rem;
	transition: -webkit-transform 0.2s linear;
	transition: transform 0.2s linear;
	transition: transform 0.2s linear, -webkit-transform 0.2s linear;
}

.user:not(.online-users .user):hover {
	-webkit-transform: translateX(10px);
	        transform: translateX(10px);
}

.online img {
	width: 2rem;
}

.online h3 {
	font-weight: 500;
	font-size: 0.8rem;
}

.online-users {
	display: flex;
	grid-gap: 0.5rem;
	gap: 0.5rem;
	width: 100%;
	overflow-x: auto;
	flex-shrink: 0;
	scrollbar-width: 0;
}

.online-users::-webkit-scrollbar {
	display: none;
}

.online-users > * {
	flex-shrink: 0;
}

